class Paginator {
    constructor(items, limit) {
        this.items = items || [];
        this.limit = limit || 3;
        this.paginatedItems = this.getPaginatedItems();
    }
    getPagesCount() {
        return Math.ceil(this.items.length / this.limit);
    }
    getPaginatedItems() {
        const paginatedItems = [];
        const pagesCount = this.getPagesCount();
        for (let i = 1; i <= pagesCount; i += 1) {
            const startIdx = (i - 1) * this.limit;
            const templastIdx = ((i - 1) * this.limit) + this.limit - 1;
            const lastIdx = templastIdx >= this.items.length ? this.items.length : templastIdx;
            paginatedItems.push(this.items.slice(startIdx, lastIdx + 1));
        }
        return paginatedItems;
    }
    getPageItems(pageNum) {
        return this.paginatedItems[+pageNum - 1];
    }
    setItems(items) {
        this.items = items;
        this.paginatedItems = this.getPaginatedItems();
    }
    setLimit(limit) {
        this.limit = limit;
        this.paginatedItems = this.getPaginatedItems();
    }
}
export default Paginator;
